@import '_variables';

.layer-popup.style-a {
	overflow: hidden;
	visibility: hidden;
	opacity: 1;
	position: fixed;
	z-index: -1;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #fff;
	outline: 0;

	&.active {
		visibility: visible;
		opacity: 1;
		z-index: 999;

		@at-root {
			body.active-layer-popup {
				// position: fixed;
				height: 100%;
				overflow: hidden;
			}
		}
	}

	.popup-item {
		display: flex;
		flex-direction: column;
		justify-content: stretch;
		height: 100%;

		.popup-area {
			display: flex;
			flex-direction: column;
			justify-content: stretch;
			height: 100%;
		}

		.popup-head {
			position: relative;
			height: 3rem;
			min-height: 3rem;
			box-shadow: inset 0 -0.0625rem 0 0 $color-base-020;

			.popup-title {
				display: flex;
				justify-content: center;
				align-items: center;
				position: absolute;
				top: 0;
				right: 4rem;
				left: 3.7rem;

				/* 확인 후 삭제 */
				margin: 0;
				height: 3rem;
				font-weight: 400;
				@include font-body(medium);

				svg {
					vertical-align: top;
				}

				/* 확인 후 삭제 */	
				.popup-name {
					@include font-body(medium);

					display: inline-block;
					vertical-align: top;					
					margin: 0;
					font-weight: 400;	
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					color: $color-base-090;
				}

				a.popup-name {
					text-decoration: none;
				}
			}

			.popup-control {
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 0 1rem;
				height: 100%;
	
				.control-item {
					display: block;
	
					.control-name {
						display: block;
	
						svg {
							display: block;
							fill: $color-base-090;
						}
					}
				}
			}
		}
		.popup-body {
			flex-grow: 1;
			overflow: hidden;
			overflow-y: auto;
			-webkit-overflow-scrolling: touch;
			
			// padding: 0 1.25rem;
			// >* {
			// 	margin-top: 1.5rem;
			// 	margin-bottom: 1.5rem;
			// }
		}

		.popup-control {
			position: relative;
			// height: 3rem;
			// min-height: 3rem;
			box-shadow: 0 -0.0625rem 0 0 $color-base-030;

			.button-area {
				display: flex;
				padding: 0.375rem;

				.btn {
					&.flex {
						flex-grow: 1;
						width: 100%;
					}
					+.btn.x-large {
						margin-left: 0.25rem;
					}
				}
			}
		}
	}

	.popup-close {
		position: absolute;
		top: .75rem;
		right: 1rem;
		width: 1.5rem;
		height: 1.5rem;
	}
}

.layer-popup.style-b {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	z-index: -1;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	padding: 0 1.25rem;
	visibility: hidden;
	opacity: 0;
	transition: z-index 0.2s ease, visibility 0.2s ease, opacity 0.2s ease;
	
	&::before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		visibility: visible;
		opacity: 1;
		background-color: rgba(0,0,0,0.6);
		transition: visibility 0.2s ease, opacity 0.2s ease, background-color 0.2s ease;
	}

	&.type-a {
		.popup-item {
			width: 280px;
			max-width: 100%;
		}
	}

	&.type-b {
		.popup-item {
			width: 480px;
			max-width: 100%;
		}
	}

	.popup-item {
		visibility: hidden;
		opacity: 0;
		transform: translateY(1rem);
		transition: visibility 0.5s ease, opacity 0.5s ease, transform 0.5s ease;
		transition-delay: 0.0.5s;
		overflow: hidden;
		border-radius: 0.25rem;
		background-color: #fff;

		.popup-area {
			padding: 1.25rem;

			.popup-head {
				margin-bottom: 1rem;

				.popup-title {
					margin: 0;
					font-weight: 700;
					@include font-body(medium);
					color: $color-base-090;
	
					.popup-name {
	
					}
				}
			}
	
			.popup-body {
			}
		}


		.popup-control {
			box-shadow: 0 -0.0625rem 0 0 $color-base-020;

			.button-area {
				display: flex;

				.btn {
					flex-grow: 1;
					display: flex;
					justify-content: stretch;
					align-items: stretch;
					width: 100%;
					@include font-body(small);
					color: $color-base-090;

					.btn-text {
						flex-grow: 1;
						display: flex;
						justify-content: center;
						align-items: center;
						height: 3rem;
					}

					+.btn {
						border-left: 0.0625rem solid $color-base-020;
					}
				}
			}
		}
	}


	&.active {
		z-index: 999;
		visibility: visible;
		opacity: 1;

		&::before {
			visibility: visible;
			opacity: 1;
			background-color: rgba(0,0,0,0.6);
		}

		.popup-item {
			visibility: visible;
			opacity: 1;
			transform: translateY(0);
		}
	}
}

