.options-popup {
	position: fixed;
	z-index: -1;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	visibility: hidden;
	opacity: 0;
	transition: z-index 0.2s ease, visibility 0.2s ease, opacity 0.2s ease;
	
	// 상품 옵션창 dimmed 삭제
	// &::before {
	// 	content: '';
	// 	position: absolute;
	// 	top: 0;
	// 	right: 0;
	// 	bottom: 0;
	// 	left: 0;
	// 	visibility: visible;
	// 	opacity: 1;
	// 	background-color: rgba(0,0,0,0.6);
	// 	transition: visibility 0.2s ease, opacity 0.2s ease, background-color 0.2s ease;
	// }

	.popup-item {
		display: flex;
		flex-direction: column;
		justify-content: stretch;
		position: absolute;
		right: 0;
		bottom: 0;
		left: 0;
		max-height: calc(100% - 3rem);
		overflow: hidden;
		background-color: #fff;
		border-top-right-radius: .5rem;
		border-top-left-radius: .5rem;
		box-shadow: 0 -2px 4px 0 rgba(0, 0, 0, 0.08);
		visibility: hidden;
		opacity: 0;
		transform: translateY(3rem);
		transition: visibility .2s ease, opacity .2s ease, transform .2s ease;
		transition-delay: .0.5s;

		.popup-area {
			flex-grow: 1;
			display: flex;
			flex-direction: column;
			justify-content: stretch;
			box-sizing: border-box;
			max-height: inherit;
			overflow-y: auto;
			-webkit-overflow-scrolling: touch;
			
			.popup-head {
				@include hidden;
			}

			.popup-body {
				flex-grow: 1;
				overflow-y: auto;
				-webkit-overflow-scrolling: touch;
				padding: 2.5rem 0.5rem 0 0.5rem;

				@include respond-above(x-small) {
					padding: 2.5rem 1rem 0 1rem;
				}
				@include respond-above(small) {
					padding: 2.5rem 1.25rem 0 1.25rem;
				}

				// 옵션설정
				.section.option-setting {
					+.section.quantity-setting {
						position: relative;
						border-top: 0.5rem solid $color-base-020;

						&::before {
							content: '';
							display: block;
							position: absolute;
							left: calc(50% - 50vw);
							top: -.5rem;
							width: 100vw;
							height: .5rem;
							background-color: #f4f4f4;
						}
					}
					.section-head {
						@include hidden;
					}

					.section-body {
						.submit-form {
							margin-bottom: 1rem;

							&.type-a {
								.form-list {
									.form-item {
										.form-head {
											@include hidden;
										}
									}
								}
							}

							.form-list {
								.form-item {
									.form-head {
										margin-bottom: 0.25rem;
										@include font-body(small);
										color: $color-base-090;
									}

									.form-body {
										>*+* {
											margin-top: 0.25rem;
										}
										.form-area {
											display: flex;
											>*+* {
												margin-left: 0.25rem;
											}
											.flex {
												flex-grow: 1;
											}
										}

										.form-noti {
											text-align: right;
										}
									}
								}
							}
						}
					}
				}

				// 수량선택
				.section.quantity-setting {
					.section-head {
						@include hidden;
					}
					.section-body {
						.submit-form {
							.form-list {
								.form-item {
									padding: 1rem 0;
									+.form-item {
										box-shadow: 0 -0.0625rem 0 0 $color-base-020;
									}
									.form-head {
										display: flex;
										flex-wrap: wrap;
										margin-bottom: 1rem;

										.form-name {
											display: block;
											width: 100%;
											@include font-body(small);
											color: $color-base-070;
										}
										.btn {
											vertical-align: top;
										}
									}	
									.form-body {
										.form-area {
											display: flex;
											justify-content: space-between;
											align-items: center;
											.form-data {
												display: flex;
												align-items: center;
												@include font-body(small);
												color: $color-base-090;
		
												.btn {
													margin-left: 0.5rem;
		
													svg {
														fill: $color-base-050;
														width: 1.25rem;
														height: 1.25rem;
													}
												}
											}
										}
									}
								}
							}
						}
					}
				}


				// >* {
				// 	margin-top: 2rem;
				// 	margin-bottom: 2rem;
				// }

				.button-area.add-item {
					display: flex;
					justify-content: center;
					align-items: center;
					position: relative;
					padding: 1rem 0;
					box-shadow: 0 -.0625rem 0 0 $color-base-020;

					&::before {
						content: '';
						display: block;
						position: absolute;
						left: calc(50% - 50vw);
						top: -.0625rem;
						width: 100vw;
						height: .0625rem;
						background-color: #f4f4f4;
					}
				}
			}

			&::after {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				right: 0;
				left: 0;
				height: 2.5rem;
				background: linear-gradient(180deg, rgba(255,255,255,1) 75%, rgba(255,255,255,0) 100%);
				// background-color: red;
			}

			.popup-control {
				position: relative;
				box-shadow: 0 -.0625rem 0 0 $color-base-030;
				
				.option-foot {
					padding: 0 1.25rem;

					.option-list {
						margin: 0.75rem 0;

						.option-item {
							@include font-body(x-small);
							color: $color-base-060;
							text-align: right;
						}
					}
					
					.price-data {
						display: flex;
						justify-content: flex-end;
						align-items: center;
						margin: .75rem 0;
						color: $color-base-090;

						.head {
							font-weight: 700;
							@include font-body(small);
						}
						.body {
							.value {
								font-weight: 700;
								@include font-head(small);
							}
							.unit {
								font-weight: 700;
								@include font-body(small);
							}
						}
					}
				}

				.npay-area {
					padding: 0 1.25rem 0.5rem 1.25rem;
				}
	
				.button-area {
					display: flex;
					padding: 0.375rem;
	
					.btn {
						&.flex {
							flex-grow: 1;
						}
						+.btn.x-large {
							margin-left: 0.25rem;
						}
					}
				}
			}
		}

		.btn.popup-close {
			position: absolute;
			top: .5rem;
			/*left: calc(50% - .75rem);
			width: 1.5rem;
			height: 1.5rem;*/
			// background-color: red;
			display: flex;
			justify-content: center;
			width: 100%;
			height: 1.5rem;

			svg {
				fill: $color-base-040;
			}
		}

	}

	&.active {
		@at-root {
			body.active-info-popup {
				height: 100%;
				overflow: hidden;
			}
		}

		z-index: 999;
		visibility: visible;
		opacity: 1;

		&::before {
			visibility: visible;
			opacity: 1;
			background-color: rgba(0,0,0,0.6);
		}

		.popup-item {
			visibility: visible;
			opacity: 1;
			transform: translateY(0);

		}
	}
}

// 이용약관
#termsOfService {
	.popup-body {
		padding: 1.5rem 0.5rem 2.5rem 0.5rem;
		@include respond-above(x-small) {padding: 1.5rem 1rem 2.5rem 1rem;}
		@include respond-above(small) {padding: 1.5rem 1.25rem 2.5rem 1.25rem;}
	}
}

// 개인정보처리방침
#privacyPolicyAll {
	.popup-body {
		padding: 1.5rem 0.5rem 2.5rem 0.5rem;
		@include respond-above(x-small) {padding: 1.5rem 1rem 2.5rem 1rem;}
		@include respond-above(small) {padding: 1.5rem 1.25rem 2.5rem 1.25rem;}
	}
}

// 배송지선택
#selectAddress {
	.popup-body {
		padding: 1rem 1.25rem;
	}
}

// 주소 찾기
#searchAddress {
	.popup-body {
		padding: 1rem 1.25rem;
	}
}

// 개인정보 제3자 제공
#privacyPolicy {
	.popup-body {
		padding: 1rem 1.25rem;
	}
}

// 관심사 선택
#choiceOfInterest {
	.popup-head {
		box-shadow: none;
		.popup-title {
			@include hidden;
		}
	}
	.popup-body {
		padding: 0 1.25rem;

		>* {
			margin: 1.25rem 0;
		}
	}
}

// 판매자 정보
#sellerInformation {
	.popup-item {
		max-width: 380px;

		.popup-body {
			.button-area {
				margin-top: 1rem;
			}
		}
	}
}

// 사진 후기
#photoReviewList {
	.popup-head {
		.popup-title {
			.total-data {
				margin-left: .25rem;
				color: $color-base-090;
			}
		}
	}
	.popup-body {
		padding: 0 0.5rem;
		@include respond-above(x-small) {padding: 0 1rem;}
		@include respond-above(small) {padding: 0 1.25rem;}

		.posts-display {
			margin: 1.5rem 0;
		}
	}
}

// 포토/영상리뷰
#photoReviewRead {
	.popup-head {
		.popup-title {
			.total-data {
				margin-left: 0.25rem;
				color: $color-base-090;
			}
		}
	}
	.popup-body {
		display: flex;
		overflow: hidden;
		padding-bottom: 8.875rem;

		.stage {
			flex-grow: 1;
			display: flex;
			justify-content: center;
			align-items: center;

			position: relative;
			// height: calc(100% - 8.875rem);
			// margin-bottom: 8.875rem;
			// box-sizing: border-box;

			img {
				display: block;
				margin: 0 auto;
				// max-height: 100%;
			}

			.type-video {
				position: relative;
				width: 100%;
				padding-top: 56.25%;
				background-color: $color-base-050;

				.btn.play {
					display: flex;
					align-items: center;
					flex-direction: column;
					position: absolute;
					top: calc(50% - 1.875rem);
					left: calc(50% - 1.875rem);
					width: 3.75rem;
					@include font-body(small);
					color: #fff;

					&::before {
						content: '';
						display: block;
						margin-bottom: .5rem;
						width: 2rem;
						height: 2rem;
						background-repeat: no-repeat;
						background-size: 100% auto;
						background-image: svgIcon(play, #fff);
					}
				}
			}

			.pagination {
				.btn {
					position: absolute;
					top: calc(50% - 1.5rem);

					&.prev {
						left: 0.125rem;
					}
					&.next {
						right: 0.125rem;
					}

					svg {
						width: 3rem;
						height: 3rem;
						opacity: 0.1;
					}
				}
			}
		}


		.posts-display {
			position: absolute;
			right: 0;
			bottom: 0;
			left: 0;
