				}
				.drawing-start {
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-items: center;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					margin: 0;
					padding: 1rem 0;
					width: 15rem;
					line-height: 1.25rem;
					font-size: .875rem;
					color: #1c1814;
