					border-radius: .25rem;
					box-shadow: 0 .125rem .25rem 0 rgba(0, 0, 0, .08);
					background-color: #fff;
					cursor: pointer;

					&::before {
						content: '';
						position: relative;
						z-index: 1;
						display: block;
						margin-bottom: .5rem;
						width: 1.5rem;
						height: 2.25rem;
						background-repeat: no-repeat;
						background-position: right bottom;
						background-size: 1.5rem 1.5rem;


						background-image: svgIcon(touch, $color-primary-070);
					}
					&::after {
						content: '';
						display: block;
						position: absolute;
						top: 1rem;
						left: calc(50% - 1rem);
						width: 1.5rem;
						height: 1.5rem;
						border-radius: 100%;
						background-color: #62d7ab;
						-webkit-animation: spinner-touch 1.5s infinite ease-out;
