					color: $color-base-050;
				}
			}
			.data-list.style-a.type-a {
				margin-top: 1.5rem;
			}

			.button-area {
				margin-top: 1.5rem;
			}

			.section {
				margin: 1.5rem 0;

				.section-head {
					display: flex;
					justify-content: space-between;
					align-items: center;
					margin-bottom: 0.5rem;
	
					.section-title {
						display: flex;
						align-items: center;

						.section-name {
							margin: 0;
							@include font-body(medium);
							color: $color-base-090;
						}

