// 상품 검색 필터
#searchFilter {
	.popup-item {
		.popup-body {
			display: flex;
			overflow: hidden;
			background-color: $color-base-010;

			.tab-display {
				width: 8rem;
				min-width: 8rem;
				overflow-y: auto;
				-webkit-overflow-scrolling: touch;

				.tab-list {
					margin: 0;
					padding: 0.5rem;

					.tab-item {
						list-style: none;

						+.tab-item {
							margin-top: 0.25rem;
						}

						.tab-name {
							display: flex;
							align-items: center;
							padding: 0 0.75rem;
							height: 2rem;
							@include font-body(small);
							color: $color-base-060;
							text-decoration: none;
						}

						&.current {
							.tab-name {
								font-weight: 700;
								color: $color-primary-080;
								border-radius: 1rem;
								background-color: #fff;
								box-shadow: 0 0.0625rem 0.1875rem 0 rgba(0,0,0,0.08);
							}
						}
					}
				}
			}

			.tab-content {
				flex-grow: 1;
				overflow-y: auto;
				-webkit-overflow-scrolling: touch;
				position: relative;
			}
			
			.section.default {
				padding: 1rem 1.25rem;
				background-color: #fff;

				+.section.default {
					box-shadow: 0 -0.0625rem 0 $color-base-020;
				}


				&:last-child {
					box-sizing: border-box;
					min-height: calc(100% + 1px);
				}

				.section-head {
					display: flex;
					justify-content: space-between;
					align-items: center;
					margin: 0 0 0.75rem 0;

					.section-title {
						margin: 0;
						font-weight: 700;
						@include font-body(small);
						color: $color-base-090;
						white-space: nowrap;
					}

					.section-intro {

						.price-range {
							margin: 0;
							@include font-body(x-small);
							color: $color-primary-080;
							text-align: right;

							* {
								display: inline-block;
								vertical-align: top;
								white-space: nowrap;
							}
						}
					}
				}
			}
		}
	}
}